import React, { useState, useEffect } from 'react';
import { useAuth } from '../../hooks/Auth/useAuth';
import { obterTiposEventos, salvarTiposEventos } from '../../hooks/FireBase/firebaseconfig';
import './EventTypesManager.css';

const EventTypesManager = ({ onSave, onClose }) => {
  const { user } = useAuth();
  const [tipos, setTipos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [novoTipo, setNovoTipo] = useState({
    nome: '',
    cor: '#2196F3',
    icone: '📅',
    descricao: '',
    prioridade: 'normal',
    categoria: 'geral',
    publico: true,
    configuracoes: {
      duracaoPadrao: 60,
      lembrete: true,
      tempoLembrete: 15,
      notificacoes: true,
      recorrencia: false
    }
  });

  const icones = [
    // Trabalho/Negócios
    '📅', '🤝', '💼', '📊', '📈', '📉', '💰', '�', '📋', '📌', '📝', '✅', '❌', '⚠️', '🔔',
    // Eventos/Celebrações
    '�🎉', '🎊', '🎂', '🎁', '🏆', '🥇', '🎭', '🎪', '🎨', '🎵', '🎤', '🎬', '📸', '🎮',
    // Tempo/Cronologia
    '⏰', '⏲️', '⏱️', '🕐', '🕑', '🕒', '🕓', '🕔', '🕕', '🕖', '🕗', '⏳', '⌛', '📅', '📆',
    // Educação/Aprendizado
    '📚', '📖', '📓', '📔', '📕', '📗', '📘', '📙', '�', '🏫', '✏️', '🖊️', '🖍️', '📐', '🔍',
    // Saúde/Bem-estar
    '🏥', '💊', '🩺', '💉', '🧘', '🏃', '🚴', '🏊', '⚽', '🏀', '🎾', '🧠', '❤️', '💪',
    // Viagem/Lazer
    '✈️', '🚗', '🚌', '🚂', '🛳️', '�🏖️', '🏔️', '🏕️', '�️', '🧳', '�', '�', '🌎', '🌏',
    // Casa/Família
    '🏠', '🏡', '�‍👩‍👧‍👦', '👶', '�️', '�', '🧹', '�️', '🔧', '🔨', '�', '🎀', '💝', '🌹',
    // Tecnologia
    '💻', '📱', '⌨️', '🖥️', '🖨️', '📡', '🔌', '💾', '💿', '📀', '�', '📺', '�', '�',
    // Natureza/Clima
    '🌞', '🌙', '⭐', '🌈', '☀️', '⛅', '🌧️', '⛈️', '❄️', '🌸', '🌺', '🌻', '🌲', '�'
  ];

  const categorias = [
    { id: 'geral', nome: 'Geral', cor: '#6c757d' },
    { id: 'trabalho', nome: 'Trabalho', cor: '#007bff' },
    { id: 'pessoal', nome: 'Pessoal', cor: '#28a745' },
    { id: 'educacao', nome: 'Educação', cor: '#17a2b8' },
    { id: 'saude', nome: 'Saúde', cor: '#dc3545' },
    { id: 'lazer', nome: 'Lazer', cor: '#fd7e14' },
    { id: 'familia', nome: 'Família', cor: '#e83e8c' },
    { id: 'financeiro', nome: 'Financeiro', cor: '#20c997' }
  ];

  const prioridades = [
    { id: 'baixa', nome: 'Baixa', cor: '#28a745' },
    { id: 'normal', nome: 'Normal', cor: '#17a2b8' },
    { id: 'alta', nome: 'Alta', cor: '#ffc107' },
    { id: 'urgente', nome: 'Urgente', cor: '#dc3545' }
  ];

  useEffect(() => {
    const carregarTipos = async () => {
      if (user) {
        try {
          const tiposCarregados = await obterTiposEventos(user.uid);
          setTipos(tiposCarregados);
        } catch (error) {
          console.error('Erro ao carregar tipos:', error);
        } finally {
          setLoading(false);
        }
      }
    };

    carregarTipos();
  }, [user]);

  const handleAddTipo = () => {
    if (novoTipo.nome.trim()) {
      const novoId = novoTipo.nome.toLowerCase().replace(/\s+/g, '-').replace(/[^a-z0-9-]/g, '');
      const tipo = {
        id: novoId,
        nome: novoTipo.nome.trim(),
        cor: novoTipo.cor,
        icone: novoTipo.icone,
        descricao: novoTipo.descricao.trim(),
        prioridade: novoTipo.prioridade,
        categoria: novoTipo.categoria,
        publico: novoTipo.publico,
        configuracoes: novoTipo.configuracoes,
        dataCriacao: new Date().toISOString()
      };
      
      setTipos([...tipos, tipo]);
      setNovoTipo({ 
        nome: '', 
        cor: '#2196F3', 
        icone: '📅', 
        descricao: '', 
        prioridade: 'normal', 
        categoria: 'geral',
        publico: true,
        configuracoes: {
          duracaoPadrao: 60,
          lembrete: true,
          tempoLembrete: 15,
          notificacoes: true,
          recorrencia: false
        }
      });
    }
  };

  const handleRemoveTipo = (id) => {
    setTipos(tipos.filter(tipo => tipo.id !== id));
  };

  const handleEditTipo = (id, field, value) => {
    setTipos(tipos.map(tipo => 
      tipo.id === id ? { ...tipo, [field]: value } : tipo
    ));
  };

  const handleSave = async () => {
    setSaving(true);
    try {
      await salvarTiposEventos(user.uid, tipos);
      if (onSave) {
        onSave(tipos);
      }
      onClose();
    } catch (error) {
      console.error('Erro ao salvar tipos:', error);
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <div className="event-types-loading">
        <div className="spinner"></div>
        <p>Carregando tipos de eventos...</p>
      </div>
    );
  }

  return (
    <div className="event-types-overlay">
      <div className="event-types-container">
        <div className="event-types-header">
          <h2>🎯 Gerenciar Tipos de Eventos</h2>
          <button onClick={onClose} className="close-button">×</button>
        </div>

        <div className="event-types-content">
          <div className="templates-section">
            <h3>🚀 Templates Rápidos</h3>
            <p className="section-subtitle">Clique para adicionar tipos pré-configurados</p>
            <div className="templates-grid">
              {[
                { 
                  nome: 'Reunião Equipe', 
                  icone: '👥', 
                  cor: '#007bff', 
                  categoria: 'trabalho', 
                  prioridade: 'normal', 
                  descricao: 'Reuniões com a equipe de trabalho',
                  publico: true,
                  configuracoes: { duracaoPadrao: 60, lembrete: true, tempoLembrete: 15, notificacoes: true, recorrencia: true }
                },
                { 
                  nome: 'Apresentação', 
                  icone: '📊', 
                  cor: '#28a745', 
                  categoria: 'trabalho', 
                  prioridade: 'alta', 
                  descricao: 'Apresentações e demonstrações',
                  publico: true,
                  configuracoes: { duracaoPadrao: 90, lembrete: true, tempoLembrete: 30, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Workshop', 
                  icone: '🛠️', 
                  cor: '#17a2b8', 
                  categoria: 'educacao', 
                  prioridade: 'normal', 
                  descricao: 'Workshops e treinamentos práticos',
                  publico: true,
                  configuracoes: { duracaoPadrao: 180, lembrete: true, tempoLembrete: 60, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Aniversário', 
                  icone: '🎂', 
                  cor: '#e83e8c', 
                  categoria: 'pessoal', 
                  prioridade: 'baixa', 
                  descricao: 'Aniversários e comemorações',
                  publico: true,
                  configuracoes: { duracaoPadrao: 1440, lembrete: true, tempoLembrete: 1440, notificacoes: true, recorrencia: true }
                },
                { 
                  nome: 'Consulta Médica', 
                  icone: '🏥', 
                  cor: '#dc3545', 
                  categoria: 'saude', 
                  prioridade: 'alta', 
                  descricao: 'Consultas e exames médicos',
                  publico: false,
                  configuracoes: { duracaoPadrao: 60, lembrete: true, tempoLembrete: 120, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Viagem', 
                  icone: '✈️', 
                  cor: '#fd7e14', 
                  categoria: 'lazer', 
                  prioridade: 'normal', 
                  descricao: 'Viagens e férias',
                  publico: true,
                  configuracoes: { duracaoPadrao: 1440, lembrete: true, tempoLembrete: 1440, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Pagamento', 
                  icone: '💰', 
                  cor: '#20c997', 
                  categoria: 'financeiro', 
                  prioridade: 'alta', 
                  descricao: 'Pagamentos e cobranças',
                  publico: false,
                  configuracoes: { duracaoPadrao: 30, lembrete: true, tempoLembrete: 1440, notificacoes: true, recorrencia: true }
                },
                { 
                  nome: 'Lembrete', 
                  icone: '📝', 
                  cor: '#6f42c1', 
                  categoria: 'geral', 
                  prioridade: 'baixa', 
                  descricao: 'Lembretes gerais',
                  publico: false,
                  configuracoes: { duracaoPadrao: 15, lembrete: true, tempoLembrete: 15, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Treino Academia', 
                  icone: '💪', 
                  cor: '#6c757d', 
                  categoria: 'saude', 
                  prioridade: 'normal', 
                  descricao: 'Treinos e exercícios físicos',
                  publico: true,
                  configuracoes: { duracaoPadrao: 90, lembrete: true, tempoLembrete: 30, notificacoes: true, recorrencia: true }
                },
                { 
                  nome: 'Dentista', 
                  icone: '🦷', 
                  cor: '#e91e63', 
                  categoria: 'saude', 
                  prioridade: 'alta', 
                  descricao: 'Consultas odontológicas',
                  publico: false,
                  configuracoes: { duracaoPadrao: 60, lembrete: true, tempoLembrete: 120, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Cinema', 
                  icone: '🎬', 
                  cor: '#795548', 
                  categoria: 'lazer', 
                  prioridade: 'baixa', 
                  descricao: 'Filmes e entretenimento',
                  publico: true,
                  configuracoes: { duracaoPadrao: 150, lembrete: true, tempoLembrete: 60, notificacoes: true, recorrencia: false }
                },
                { 
                  nome: 'Almoço de Negócios', 
                  icone: '🍽️', 
                  cor: '#ff9800', 
                  categoria: 'trabalho', 
                  prioridade: 'normal', 
                  descricao: 'Almoços e reuniões informais',
                  publico: true,
                  configuracoes: { duracaoPadrao: 120, lembrete: true, tempoLembrete: 30, notificacoes: true, recorrencia: false }
                }
              ].map((template, index) => (
                <button
                  key={index}
                  className="template-button"
                  style={{ borderColor: template.cor }}
                  onClick={() => {
                    const novoId = template.nome.toLowerCase().replace(/\s+/g, '-').replace(/[^a-z0-9-]/g, '');
                    const tipo = {
                      ...template,
                      id: novoId,
                      dataCriacao: new Date().toISOString()
                    };
                    setTipos([...tipos, tipo]);
                  }}
                >
                  <span className="template-icon">{template.icone}</span>
                  <span className="template-name">{template.nome}</span>
                  <small className="template-desc">{template.descricao}</small>
                  <div className="template-badges">
                    <span className="template-duration">{template.configuracoes.duracaoPadrao >= 1440 ? 
                      `${Math.floor(template.configuracoes.duracaoPadrao / 1440)}d` : 
                      `${template.configuracoes.duracaoPadrao}min`}</span>
                    {template.publico && <span className="template-public">Público</span>}
                  </div>
                </button>
              ))}
            </div>
          </div>

          <div className="add-type-section">
            <h3>➕ Adicionar Novo Tipo</h3>
            <div className="add-type-form">
              <div className="form-row">
                <input
                  type="text"
                  placeholder="Nome do tipo (ex: Workshop, Treinamento)"
                  value={novoTipo.nome}
                  onChange={(e) => setNovoTipo({...novoTipo, nome: e.target.value})}
                  className="type-name-input"
                />
              </div>

              <div className="form-row">
                <textarea
                  placeholder="Descrição opcional (ex: Eventos de capacitação da equipe)"
                  value={novoTipo.descricao}
                  onChange={(e) => setNovoTipo({...novoTipo, descricao: e.target.value})}
                  className="type-description-input"
                  rows="2"
                />
              </div>
              
              <div className="form-row-group">
                <div className="form-field">
                  <label>Categoria:</label>
                  <select
                    value={novoTipo.categoria}
                    onChange={(e) => setNovoTipo({...novoTipo, categoria: e.target.value})}
                    className="category-select"
                  >
                    {categorias.map(cat => (
                      <option key={cat.id} value={cat.id}>{cat.nome}</option>
                    ))}
                  </select>
                </div>

                <div className="form-field">
                  <label>Prioridade:</label>
                  <select
                    value={novoTipo.prioridade}
                    onChange={(e) => setNovoTipo({...novoTipo, prioridade: e.target.value})}
                    className="priority-select"
                  >
                    {prioridades.map(prior => (
                      <option key={prior.id} value={prior.id}>{prior.nome}</option>
                    ))}
                  </select>
                </div>

                <div className="form-field">
                  <label>Cor:</label>
                  <input
                    type="color"
                    value={novoTipo.cor}
                    onChange={(e) => setNovoTipo({...novoTipo, cor: e.target.value})}
                    className="color-picker-input"
                  />
                </div>
              </div>

              <div className="advanced-options">
                <h4>⚙️ Configurações Avançadas</h4>
                
                <div className="form-row-group">
                  <div className="form-field">
                    <label>Duração padrão (min):</label>
                    <input
                      type="number"
                      min="15"
                      max="1440"
                      value={novoTipo.configuracoes.duracaoPadrao}
                      onChange={(e) => setNovoTipo({
                        ...novoTipo, 
                        configuracoes: {
                          ...novoTipo.configuracoes,
                          duracaoPadrao: parseInt(e.target.value) || 60
                        }
                      })}
                      className="duration-input"
                    />
                  </div>

                  <div className="form-field">
                    <label>Lembrete (min antes):</label>
                    <select
                      value={novoTipo.configuracoes.tempoLembrete}
                      onChange={(e) => setNovoTipo({
                        ...novoTipo, 
                        configuracoes: {
                          ...novoTipo.configuracoes,
                          tempoLembrete: parseInt(e.target.value)
                        }
                      })}
                      className="reminder-select"
                    >
                      <option value="0">Sem lembrete</option>
                      <option value="5">5 minutos</option>
                      <option value="15">15 minutos</option>
                      <option value="30">30 minutos</option>
                      <option value="60">1 hora</option>
                      <option value="120">2 horas</option>
                      <option value="1440">1 dia</option>
                      <option value="10080">1 semana</option>
                    </select>
                  </div>
                </div>

                <div className="checkbox-group">
                  <label className="checkbox-label">
                    <input
                      type="checkbox"
                      checked={novoTipo.publico}
                      onChange={(e) => setNovoTipo({...novoTipo, publico: e.target.checked})}
                    />
                    📋 Visível no calendário público
                  </label>

                  <label className="checkbox-label">
                    <input
                      type="checkbox"
                      checked={novoTipo.configuracoes.lembrete}
                      onChange={(e) => setNovoTipo({
                        ...novoTipo, 
                        configuracoes: {
                          ...novoTipo.configuracoes,
                          lembrete: e.target.checked
                        }
                      })}
                    />
                    🔔 Ativar lembretes por padrão
                  </label>

                  <label className="checkbox-label">
                    <input
                      type="checkbox"
                      checked={novoTipo.configuracoes.notificacoes}
                      onChange={(e) => setNovoTipo({
                        ...novoTipo, 
                        configuracoes: {
                          ...novoTipo.configuracoes,
                          notificacoes: e.target.checked
                        }
                      })}
                    />
                    📱 Notificações push
                  </label>

                  <label className="checkbox-label">
                    <input
                      type="checkbox"
                      checked={novoTipo.configuracoes.recorrencia}
                      onChange={(e) => setNovoTipo({
                        ...novoTipo, 
                        configuracoes: {
                          ...novoTipo.configuracoes,
                          recorrencia: e.target.checked
                        }
                      })}
                    />
                    🔄 Permitir recorrência por padrão
                  </label>
                </div>
              </div>
              
              <div className="icon-selection">
                <label>Ícone:</label>
                <div className="icon-options">
                  <div className="icon-grid">
                    {icones.map(icone => (
                      <button
                        key={icone}
                        className={`icon-option ${novoTipo.icone === icone ? 'selected' : ''}`}
                        onClick={() => setNovoTipo({...novoTipo, icone})}
                      >
                        {icone}
                      </button>
                    ))}
                  </div>
                  <div className="custom-icon-section">
                    <label>Ou digite um emoji personalizado:</label>
                    <input
                      type="text"
                      placeholder="Ex: 🌟, 🔥, 💎"
                      value={novoTipo.icone}
                      onChange={(e) => setNovoTipo({...novoTipo, icone: e.target.value})}
                      className="custom-icon-input"
                      maxLength="4"
                    />
                  </div>
                </div>
              </div>
              
              <div className="type-preview-section">
                <h4>👁️ Preview do Tipo</h4>
                <div className="type-preview-card" style={{ borderColor: novoTipo.cor }}>
                  <div className="preview-header" style={{ backgroundColor: novoTipo.cor + '20', borderColor: novoTipo.cor }}>
                    <span className="preview-icon" style={{ backgroundColor: novoTipo.cor }}>
                      {novoTipo.icone}
                    </span>
                    <div className="preview-info">
                      <h5>{novoTipo.nome || 'Nome do Tipo'}</h5>
                      <p>{categorias.find(c => c.id === novoTipo.categoria)?.nome || 'Categoria'} • {prioridades.find(p => p.id === novoTipo.prioridade)?.nome || 'Prioridade'}</p>
                    </div>
                  </div>
                  {novoTipo.descricao && (
                    <div className="preview-description">
                      {novoTipo.descricao}
                    </div>
                  )}
                  <div className="preview-details">
                    <div className="detail-item">
                      <span>⏱️ Duração: {novoTipo.configuracoes.duracaoPadrao}min</span>
                    </div>
                    <div className="detail-item">
                      <span>🔔 Lembrete: {novoTipo.configuracoes.tempoLembrete > 0 ? 
                        `${novoTipo.configuracoes.tempoLembrete >= 1440 ? 
                          `${Math.floor(novoTipo.configuracoes.tempoLembrete / 1440)} dia${Math.floor(novoTipo.configuracoes.tempoLembrete / 1440) > 1 ? 's' : ''}` :
                          `${novoTipo.configuracoes.tempoLembrete}min`} antes` : 
                        'Desativado'}</span>
                    </div>
                    <div className="detail-item">
                      <span>{novoTipo.publico ? '🌐 Público' : '🔒 Privado'}</span>
                    </div>
                  </div>
                </div>
              </div>

              <button onClick={handleAddTipo} className="add-type-button">
                ➕ Criar Tipo Personalizado
              </button>
            </div>
          </div>

          <div className="existing-types-section">
            <h3>📋 Tipos Existentes ({tipos.length})</h3>
            <div className="types-list">
              {tipos.map(tipo => (
                <div key={tipo.id} className="type-item">
                  <div className="type-preview-expanded" style={{ backgroundColor: tipo.cor }}>
                    <div className="type-header">
                      <span className="type-icon">{tipo.icone}</span>
                      <span className="type-name">{tipo.nome}</span>
                      <span className="type-category">
                        {categorias.find(c => c.id === tipo.categoria)?.nome || 'Geral'}
                      </span>
                    </div>
                    {tipo.descricao && (
                      <div className="type-description">{tipo.descricao}</div>
                    )}
                    <div className="type-priority">
                      Prioridade: {prioridades.find(p => p.id === tipo.prioridade)?.nome || 'Normal'}
                    </div>
                  </div>
                  
                  <div className="type-controls-expanded">
                    <div className="control-row">
                      <label>Nome:</label>
                      <input
                        type="text"
                        value={tipo.nome}
                        onChange={(e) => handleEditTipo(tipo.id, 'nome', e.target.value)}
                        className="edit-input"
                      />
                    </div>

                    <div className="control-row">
                      <label>Descrição:</label>
                      <textarea
                        value={tipo.descricao || ''}
                        onChange={(e) => handleEditTipo(tipo.id, 'descricao', e.target.value)}
                        className="edit-textarea"
                        rows="2"
                      />
                    </div>

                    <div className="control-row-group">
                      <div className="control-field">
                        <label>Categoria:</label>
                        <select
                          value={tipo.categoria || 'geral'}
                          onChange={(e) => handleEditTipo(tipo.id, 'categoria', e.target.value)}
                          className="edit-select"
                        >
                          {categorias.map(cat => (
                            <option key={cat.id} value={cat.id}>{cat.nome}</option>
                          ))}
                        </select>
                      </div>

                      <div className="control-field">
                        <label>Prioridade:</label>
                        <select
                          value={tipo.prioridade || 'normal'}
                          onChange={(e) => handleEditTipo(tipo.id, 'prioridade', e.target.value)}
                          className="edit-select"
                        >
                          {prioridades.map(prior => (
                            <option key={prior.id} value={prior.id}>{prior.nome}</option>
                          ))}
                        </select>
                      </div>

                      <div className="control-field">
                        <label>Cor:</label>
                        <input
                          type="color"
                          value={tipo.cor}
                          onChange={(e) => handleEditTipo(tipo.id, 'cor', e.target.value)}
                          className="edit-color"
                        />
                      </div>

                      <div className="control-field">
                        <label>Ícone:</label>
                        <input
                          type="text"
                          value={tipo.icone}
                          onChange={(e) => handleEditTipo(tipo.id, 'icone', e.target.value)}
                          className="edit-icon-input"
                          maxLength="4"
                        />
                      </div>
                    </div>
                    
                    <button
                      onClick={() => handleRemoveTipo(tipo.id)}
                      className="remove-type-button"
                    >
                      🗑️ Remover
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        <div className="event-types-actions">
          <button onClick={onClose} className="cancel-button">
            Cancelar
          </button>
          <button 
            onClick={handleSave} 
            disabled={saving}
            className="save-button"
          >
            {saving ? '⏳ Salvando...' : '💾 Salvar Tipos'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default EventTypesManager;
